import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import '@servicenow/now-dropdown';

const actionHandlers = {
	'NOW_DROPDOWN#SELECTED_ITEMS_SET': {
		effect: ({
			state: {
				properties: { onChange }
			},
			action: {
				payload: {
					value: [selectedItem]
				}
			}
		}) => {
			onChange({
				value: selectedItem
			});
		},
		stopPropagation: true
	},

	'NOW_DROPDOWN#OPENED_SET': {
		effect: ({ updateProperties, action, state }) => {
			const { value: opened } = action.payload;
			updateProperties({
				opened: opened
			});
		},
		stopPropagation: true
	}
};

/**
 * Wrapper around <now-dropdown> that has manage-opened and accepts an on-change
 *
 * This is useful when we want to keep the same on-change api and don't want to register
 * a component with seismic solely to handle the actions dispatched from <now-dropdown>
 *
 *
 * ```jsx
 * <sn-record-dropdown-internal
 *		bare
 *		disabled={readonly}
 *		variant="tertiary"
 *		size="md"
 *		items={items}
 * />
 * ```
 *
 * @seismicElement sn-record-dropdown-internal
 * @summary Wrapper around <now-dropdown> that has manage-opened and accepts an on-change.
 *
 * */
createCustomElement('sn-record-dropdown-internal', {
	renderer: {
		type: snabbdom,
		view: ({ properties }) => {
			let { disabled, hideCaret, ...rest } = properties;
			return (
				<now-dropdown
					{...rest}
					{...(isAttrTrue(disabled) ? { disabled: true } : {})}
					{...(hideCaret ? { hideCaret, disabled: true } : {})}
					manage-selected-items
					manage-opened
					panel-fit-props={{ constrain: { minWidth: 100 } }}
				/>
			);
		},
		initialState: {
			opened: false
		}
	},
	properties: {
		/**
		 * Makes the trigger button border and background transparent but retains
		 * its `variant` style setting when an iconic button. If button is text or
		 * text with an icon then only works when `variant` is `secondary`,
		 * `tertiary`, or `inherit`.
		 * @type {boolean}
		 */
		bare: { default: false },

		/**
		 * An object whose items, all aria properties,
		 * @type {{ 'aria-*': string }}
		 */
		configAria: { cast: 'object' },

		/**
		 * Enable to show the dropdown as visually disabled and to stop the dropdown
		 * from opening when the user interacts with it.
		 * @type {boolean}
		 */
		disabled: { default: false },

		/**
		 * Remove the caret from the dropdown visual
		 * @type {boolean}
		 */
		hideCaret: { default: false },

		/**
		 * An array of item objects describing each item in the dropdown OR
		 * an array of objects where each object represents a section, to be
		 * displayed with divider lines, where the `label` of that object is
		 * a section header and the `children` value is an array of dropdown
		 * item objects.
		 * Each dropdown item object should have a `label` and a unique `id`.
		 * An `icon` or `presence` indicator can optionally be displayed.
		 * Items with `disabled` will be visible, but not selectable by the user.
		 * @type {({id: (string|number), label: string, count: number, icon: string, presence: string, disabled: boolean}[] | {label: string, children: Array.<Object>}[])}
		 */
		items: { default: [], cast: 'array' },

		/**
		 * Enable this property to override automatic updates to the `opened` property.
		 * An action with type `NOW_DROPDOWN#OPENED_SET` will be dispatched to request updates,
		 * which will be handled by this component.
		 *
		 * @type {boolean}
		 */
		manageOpened: { default: false },

		/**
		 * Enable this property to override automatic updates to the `selectedItems` property.
		 * An action with type `NOW_DROPDOWN#SELECTED_ITEMS_SET` will be dispatched to request updates,
		 * which will be handled by this component.
		 *
		 * @type {boolean}
		 */
		manageSelectedItems: { default: false },

		/**
		 * OnChange event callback
		 *
		 * @type {function}
		 * @private
		 */
		onChange: { default: () => {} },

		/**
		 * Automatically updated when the user opens or closes the dropdown.
		 * Use `manage-opened` to override the default behavior and handle the
		 * `NOW_DROPDOWN#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		opened: { default: false, manageable: true },

		/**
		 * An array of ids representing each selected item. If multi-select mode is
		 * not enabled, each time the user selects an item, the previously selected
		 * id will be cleared out and the new id will be added. If multi-select mode is
		 * enabled, ids will be added to or removed from the list.
		 * Use `manage-selected-items` to override the default behavior and handle the
		 * `NOW_DROPDOWN#SELECTED_ITEMS_SET` action manually.
		 * @type {(string[])[]}
		 */
		selectedItems: { default: [], cast: 'array', manageable: true },

		/**
		 * Sets the button styles of the dropdown trigger.
		 * @type {string}
		 */
		variant: {}
	},
	actionHandlers
});
